import Vue from 'vue'
import Router from 'vue-router'
import Home from './components/Home.vue'

Vue.use(Router)

export default new Router({
  routes: [
    // 网站架构
    {
      path: '/',
      name: 'home',
      component: Home,
      // 单页内容
      children: [
        {
          path: '/dashboard',
          name: '网站首页',
          component: () => import('./views/Dashboard.vue')
        },{
          path: '/poem',
          name: '诗词查询',
          component: () => import('./views/poem.vue')
        },{
          path: '/key-point',
          name: '考点查询',
          component: () => import('./views/keyPoint.vue')
        },{
          path: '/map-control',
          name: 'v0.0.2 - 地图控件',
          component: () => import('./views/mapControl.vue')
        },{
          path: '/map-symbol',
          name: 'v0.0.3 - 地图符号',
          component: () => import('./views/mapSymbol.vue')
        },{
          path: '/map-drawer',
          name: 'v0.0.4 - 地图绘图',
          component: () => import('./views/mapDrawer.vue')
        },{
          path: '/simple-markup',
          name: 'v0.0.5 - 简单标记',
          component: () => import('./views/simpleMarkup.vue')
        },{
          path: '/popup-markup',
          name: 'v0.0.6 - 弹出标记',
          component: () => import('./views/popupMarkup.vue')
        },{
          path: '/test-map',
          name: 'v0.0.0 - 测试专用',
          component: () => import('./views/testMap.vue')
        },{
          path: '/WMTS-layer',
          name: 'WMTS图层',
          component: () => import('./views/map.vue')
        },{
          path: '/html-2-img',
          name: 'v0.0.8 - 地图元素转图形',
          component: () => import('./views/htmlToImage.vue')
        }
      ]
    },
    {
      path: '/one-map',
      name: 'single-map',
      component: () => import('./views/oneMap.vue')
    },
    {
      path: '*',
      redirect: '/dashboard'
    }
  ]
})
