<template>
  <div class="header">
    <!-- 标题文字 -->
    <div class="logo">
      <el-tooltip effect="dark" :content="btnSidebarShowContent" placement="top-start">
        <div class="btn-sidebar-show" @click="onSidebarShow">
          <i class="el-icon-menu"></i>
        </div>
      </el-tooltip>

      <img src="../assets/img/logo.png"><span>阡陌 - Web 历史地图</span>
    </div>

    <div class="header-right">
      <div class="header-user-con">
        <!-- 打开编辑框 -->
        <div class="btn-editor" @click="isEditorShown = !isEditorShown">
          <el-tooltip effect="dark" :content="'点我' + (isEditorShown ? '收起编辑器' : '显示编辑器')">
            <i class="el-icon-document"></i>
          </el-tooltip>
        </div>
        <!-- 全屏显示 -->
        <div class="btn-fullscreen" @click="handleFullScreen">
          <el-tooltip effect="dark" :content="fullscreen?`取消全屏`:`全屏`" placement="bottom">
            <i class="el-icon-rank"></i>
          </el-tooltip>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import bus from './bus';
export default {
  name: 'header-component',
  computed: {
    btnSidebarShowContent: function () {
      bus.$emit('sidebar-show', this.isSidebarShown);
      return this.isSidebarShown ? '隐藏侧边栏' : '显示侧边栏';
    }
  },
  data() {
    return {
      fullscreen: false,
      isSidebarShown: true,
      isEditorShown: false,
    }
  },// data
  watch: {
    isEditorShown: function (val, oldVal) {
      if (val != oldVal) {
        bus.$emit('editor-show', val);
      }
    }
  },
  methods:{
    // 全屏事件
    handleFullScreen(){
      let element = document.documentElement;
      if (this.fullscreen) {
        if (document.exitFullscreen) {
          document.exitFullscreen();
        } else if (document.webkitCancelFullScreen) {
          document.webkitCancelFullScreen();
        } else if (document.mozCancelFullScreen) {
          document.mozCancelFullScreen();
        } else if (document.msExitFullscreen) {
          document.msExitFullscreen();
        }
      } else {
        if (element.requestFullscreen) {
          element.requestFullscreen();
        } else if (element.webkitRequestFullScreen) {
          element.webkitRequestFullScreen();
        } else if (element.mozRequestFullScreen) {
          element.mozRequestFullScreen();
        } else if (element.msRequestFullscreen) {
          // IE11
          element.msRequestFullscreen();
        }
      }
      this.fullscreen = !this.fullscreen;
    },
    onSidebarShow () {
      this.isSidebarShown = !this.isSidebarShown;
      // 特殊事件，当使用控制按钮后，移除 onresize 监听
      bus.$emit('btn-control-sidebar', this.isSidebarShown);
    }
  },//methods
  created() {
    bus.$on('sidebar-show', val => {
      this.isSidebarShown = val;
    });
  }, // created
}
</script>

<style scoped>
.header {
  position: relative;
  box-sizing: border-box;
  width: 100%;
  height: 50px;
  margin: 0;
  font-size: 22px;
  color: #fff;
  background-color: rgb(11, 15, 49);
}
.header .logo{
  float: left;
  width: auto;
  margin: 0;
  line-height: 50px;
}
.header .logo img{
  height: 30px;
  width: 30px;
  margin: 10px;
  /* clip-path: inset(10px 10px 15px 10px); */
}
.header .logo *{
  display: inline-block;
  vertical-align: middle;
}
.header-right{
  position: relative;
  float: right;
  padding-right: 15px;
}
.header-user-con{
  display: flex;
  height: 50px;
  align-items: center;
}
.btn-fullscreen{
  transform: rotate(45deg);
  margin-right: 5px;
  font-size: 24px;
  cursor: pointer;
}
.btn-sidebar-show {
  display:inline-block;
  cursor:pointer;
  height: 48px;
  width: 48px;
  margin:0;
}
.btn-editor {
  cursor: pointer;
  font-size: 24px;
  margin-right: 15px;
}
</style>
